import '@eui/base';
import { decoratorMixin } from '../utilities/decoratorMixin';

import '../icon/Icon';

export const sortable = decoratorMixin({
  /**
   * This is called each time a column is clicked. The index of the column
   * is entered as a parameter. An event is dispatched with the details of
   * the sorted column sent in the payload.
   *
   * @function _sortColumn
   * @param {Number} index - The index of the column to sort.
   * @event eui-table:sort - dispatched when a heading column is clicked.
   * @private
   */
  _sortColumn(index) {
    if (!this._dragging) {
      if (this.columns[index].sort === 'dec') {
        this.columns[index].sort = 'asc';
      } else if (this.columns[index].sort === 'asc') {
        this.columns[index].sort = 'dec';
      } else {
        this.columns[index].sort = 'asc';
      }

    this.columns.forEach((column, colIndex) => {
      if (colIndex !== index) {
        column.sort = null;
      }
    });
    this.columns = [...this.columns];

    this.bubble('eui-table:sort', { sort: this.columns[index].sort, column: this.columns[index] });
    }
  },

  /**
   * The state of the icon is changed depending on the details of the column.
   * If the column is not currently sorted then the icon is "sorting".
   * If the column is currently sorted decending, then the icon changes to "sort-down".
   * If the column is currently sorted ascending, then the icon changes to "sort-up".
   *
   * @function _iconState
   * @param {Number} index - The index of the column to sort.
   * @private
   */
  _iconState(index) {
    if (this.columns[index].sort === 'dec') {
      return 'sort-down';
    } if (this.columns[index].sort === 'asc') {
      return 'sort-up';
    }
    return 'sorting';
  },
});
