/**
 * Icon represents a simple icon component. It is defined as
 * `<eui-v[version]-icon>`
 * Where [version] is replaced with the major version number of the package.
 * The version of the package is found in project.json.
 *
 * Imperatively create a icon component
 * @example
 * let icon = new Icon();
 *
 * Declaratively create a icon component
 * @example
 * <eui-v0-icon name="icon-name"></eui-v0-icon>
 *
 * @extends {TemplateComponent}
 */
import { definition, TemplateComponent } from '@eui/component';
import style from './icon.css';
import template from './icon.html';
import mapper from '../../assets/icon-defs.json';

@definition('eui-icon', {
  style,
  template,
  props: {
    color: { attribute: true, type: 'string' },
    name: { attribute: true, type: 'string', required: true },
    size: { attribute: true, type: 'string' },
  },
})
export default class Icon extends TemplateComponent {
  constructor() {
    super();
    this.icon = this.shadowRoot.querySelector('.table__icon');
  }

  /**
   *
   * @function componentDidConnect
   * @description lifecycle hook
   * triggers when component connected to DOM.
   */
  componentDidConnect() {
    if (this.size) {
      this.icon.style.setProperty('--icon-size', this.size);
    }
    if (this.color) {
      this.icon.style.setProperty('--icon-color', this.color);
    }
    this.icon.firstElementChild.setAttribute('d', mapper[this.name]);
  }

  /**
   *
   * @function componentDidReceiveProps
   * @description lifecycle hook
   * trigger when component receives a new prop.
   * @param {Object} prev - previous props
   */
  componentDidReceiveProps(prev) {
    if (prev.name && prev.name !== this.name) {
      this.icon.firstElementChild.setAttribute('d', mapper[this.name]);
    }
    if (prev.size !== this.size) {
     this.icon.style.setProperty('--icon-size', this.size);
    }
    if (prev.color !== this.color) {
      this.icon.style.setProperty('--icon-color', this.color);
    }
  }
}
// Register eui-icon component
Icon.register();
