/**
 * Component PaginatedTable is defined as
 * `<e-paginated-table>`
 *
 * Imperatively create component
 * @example
 * let component = new PaginatedTable();
 *
 * Declaratively create component
 * @example
 * <e-paginated-table></e-paginated-table>
 *
 * @extends {LitComponent}
 */
import { definition } from '@eui/component';
import { LitComponent, html } from '@eui/lit-component';
// If the pagination arrows don't show up, import @eui/theme as below.
// import '@eui/theme';
import '../table/Table';

/**
 * @property {number} currentPage - position of the current Page
 * @property {number} numPages - number of pages
 * @property {number} numEntries - number of entries
 */
@definition('e-paginated-table', {
  props: {
    currentPage: { attribute: false, type: Number, default: 1 },
    numEntries: { attribute: false, type: Number, default: 10 },
    numPages: { attribute: false, type: Number },
    _data: { attribute: false, type: Array, default: [] },
  },
})
export default class PaginatedTable extends LitComponent {
  constructor() {
    super();
    this.tableElement = null;
    this.paginationElement = null;

    // Dummy data to send via the columns and data props to the table component
    this.columns = [
      { title: 'Column ONE', attribute: 'col1' },
      { title: 'Column TWO', attribute: 'col2' },
    ];
    this.data = [
      { col1: 'Column one, row 1', col2: 'Column two, row 1' },
      { col1: 'Column one, row 2', col2: 'Column two, row 2' },
      { col1: 'Column one, row 3', col2: 'Column two, row 3' },
      { col1: 'Column one, row 4', col2: 'Column two, row 4' },
      { col1: 'Column one, row 5', col2: 'Column two, row 5' },
      { col1: 'Column one, row 6', col2: 'Column two, row 6' },
      { col1: 'Column one, row 7', col2: 'Column two, row 7' },
      { col1: 'Column one, row 8', col2: 'Column two, row 8' },
      { col1: 'Column one, row 9', col2: 'Column two, row 9' },
      { col1: 'Column one, row 10', col2: 'Column two, row 10' },
      { col1: 'Column one, row 11', col2: 'Column two, row 11' },
      { col1: 'Column one, row 12', col2: 'Column two, row 12' },
      { col1: 'Column one, row 13', col2: 'Column two, row 13' },
      { col1: 'Column one, row 14', col2: 'Column two, row 14' },
      { col1: 'Column one, row 15', col2: 'Column two, row 15' },
      { col1: 'Column one, row 16', col2: 'Column two, row 16' },
      { col1: 'Column one, row 17', col2: 'Column two, row 17' },
      { col1: 'Column one, row 18', col2: 'Column two, row 18' },
      { col1: 'Column one, row 19', col2: 'Column two, row 19' },
      { col1: 'Column one, row 20', col2: 'Column two, row 20' },
    ];
  }

  handleEvent(event) {
    // Only handle the event from the pagination component here
    if (event.type === 'pageIndexChange') {
      event.stopPropagation();
      this.currentPage = event.detail.state.currentPage;
    }
  }

  didRender() {
    if (!this.tableElement) {
      this.tableElement = this.shadowRoot.querySelector('eui-table-v0');
    }
    if (!this.paginationElement) {
      this.paginationElement = this.shadowRoot.querySelector('eui-pagination-v0');
    }

    // Updating the numPages prop on pagination component
    if (this.paginationElement.numPages !== this.numPages) {
      this.paginationElement.numPages = this.numPages;
    }
  }

  didChangeProps(changedProps) {
    // Calculating the numPages value
    if (changedProps.has('data')) {
      this.numPages = this._calcNumPages();
    }
    
    if (changedProps.has('numEntries')) {
      this.numPages = this._calcNumPages();
    }
  }

  /**
   * Calculates numPages value.
   *
   * @function _calcNumPages
   * @private
   */
  _calcNumPages = () => {
    const dataLength = this.data.length;
    let numPages = 0;

    if (dataLength % this.numEntries !== 0) {
      numPages = parseInt(dataLength / this.numEntries, 10) + 1;
    } else {
      numPages = parseInt(dataLength / this.numEntries, 10);
    }
    return numPages;
  }

  /**
   * Filters the table data based on the values of currentPage, numPages and numEntries.
   * Returns a generated array with the filtered data
   * @function _filterData
   * @private
   */
  _filterData = () => {
    this.numPages = this._calcNumPages();

    if (this.currentPage > this.numPages) {
      this.currentPage = this.numPages;
    }
    const startIndex = (this.currentPage - 1) * this.numEntries;
    const endIndex = (this.currentPage * this.numEntries);

    // Slice is probably faster than filter in some scenarios
    // const pgData = this.data.slice(startIndex, endIndex);
    return this.data.filter((el, i) => ((i >= startIndex) && (i < endIndex)));
  }

  /**
   * Render the <e-paginated-table> component. This function is called each time a
   * prop changes.
   */
  render() {
    return html`<eui-table-v0 .data=${this._filterData()} .columns=${this.columns}></eui-table-v0>
    <eui-pagination-v0 @pageIndexChange=${this} .num-entries=${this.numEntries} .num-pages=${this.numPages}></eui-pagination-v0>`;
  }
}

/**
 * Register the component as e-paginated-table.
 * Registration can be done at a later time and with a different name
 */
PaginatedTable.register();
