/**
 * Inspired by Addy Osmani: https://medium.com/google-developers/exploring-es7-decorators-76ecb65fb841
 *
 * @param {*} behaviour
 * @param {*} sharedBehaviour
 */
export const decoratorMixin = (behaviour, sharedBehaviour = {}) => {
  const instanceKeys = Reflect.ownKeys(behaviour);
  const sharedKeys = Reflect.ownKeys(sharedBehaviour);
  const typeTag = Symbol('isa');
  function _mixin(clazz) {
    instanceKeys.forEach((instanceKey) => {
      Object.defineProperty(clazz.prototype, instanceKey, { value: behaviour[instanceKey] });
    });
    Object.defineProperty(clazz.prototype, typeTag, { value: true });
    return clazz;
  }

  sharedKeys.forEach((sharedKey) => {
    Object.defineProperty(_mixin, sharedKey, {
      value: sharedBehaviour[sharedKey],
    });
  });

  Object.defineProperty(_mixin, Symbol.hasInstance, {
    value: i => !!i[typeTag],
  });
  return _mixin;
};
