import '@eui/base';
import { html } from '@eui/lit-component';
import { decoratorMixin } from '../utilities/decoratorMixin';

export const selectable = decoratorMixin({
  /**
   * Update the supplied data with a selected state.
   *
   * @function _updateDataWithSelectedState
   * @private
   */
  _updateDataWithSelectedState() {
    this.data.forEach((row) => {
      row.selected = false;
    });
  },

  /**
   * Get the selected rows
   *
   * @function selectedRows
   * @returns {Array} selected rows
   */
  selectedRows() {
    let items = [];
    if (this.data) {
      items = this.data.filter(item => item.selected === true);
    }
    return items;
  },

  /**
   * Is the row(s) selected
   *
   * @function _isSelected
   * @returns {Boolean}
   * @private
   */
  _isSelected() {
    const tableHeadCheckbox = this.shadowRoot.querySelector('.table__heading-checkbox');
    if (this.noHeader) {
      // no header, header checkbox present in header-less table
      return;
    }
    let noOfSelectedRows = 0;
    this.data.forEach((row) => {
      if (row.selected) {
        noOfSelectedRows += 1;
      }
    });
    if (noOfSelectedRows === this.data.length && this.data.length > 0) {
      this.selectedAny = false;
      tableHeadCheckbox.checked = true;
      tableHeadCheckbox.indeterminate = false;
      tableHeadCheckbox.disabled = false;
    } else if (noOfSelectedRows > 0) {
      this.selectedAny = true;
      tableHeadCheckbox.checked = false;
      tableHeadCheckbox.indeterminate = true;
      tableHeadCheckbox.disabled = false;
    } else {
      this.selectedAny = false;
      tableHeadCheckbox.checked = false;
      tableHeadCheckbox.indeterminate = false;
    }
  },

  /**
   * Handle a click on the checkbox in the heading.
   *
   * @function _handlerCheckboxHeader
   * @param {Object} detail - detail of the event sent from the checkbox
   * @private
   */
  _handlerCheckboxHeader(detail) {
    this.data.forEach((row) => {
      if (this.selectedAny && detail.checked) {
        row.selected = !detail.checked;
      } else if (!this.selectedAny) {
        row.selected = detail.checked;
      }
    });
    setTimeout(() => {
      this.bubble('row-selected', this.selectedRows());
    }, 0);
    this.data = [...this.data];
  },

  /**
   * Handle a click on the checkbox in a row.
   *
   * @function _handlerCheckboxRow
   * @param {Object} row - row data
   * @param {Object} detail - detail from the checkbox event
   * @private
   */
  _handlerCheckboxRow(row, detail) {
    if (row && detail) {
      row.selected = detail.checked;
    }
    this.data = [...this.data];
    this._isSelected();
  },

  /**
   * Add a checkbox to the table heading
   *
   * @function addHeaderCheckbox
   * @returns {LitComponent}
   * @private
   */
  _addHeaderCheckbox() {
    return html`<th><eui-base-v0-checkbox
      class="table__heading-checkbox"
      name="row-heading"
      ?disabled=${(this.data.length === 0)}
      @change=${(event) => {
        this._handlerCheckboxHeader(event.detail);
    }}></eui-base-v0-checkbox></th>`;
  },

  /**
   * Add a checkbox to the the row of a table.
   *
   * @function addRowCheckbox
   * @param {Object} row - the current table's row
   * @private
   */
  _addRowCheckbox(row) {
    return html`<td class='checkbox'><eui-base-v0-checkbox
      name="cb-row" 
      ?checked=${row.selected}
      @change=${
      (event) => {
        this._handlerCheckboxRow(row, event.detail);
      }}></eui-base-v0-checkbox></td>`;
  },
});
