/**
 * Component Pagination is defined as
 * `<eui-pagination-v[version]>`
 *
 * Imperatively create component
 * @example
 * let component = new Pagination();
 *
 * Declaratively create component
 * @example
 * <eui-pagination-v0></eui-pagination-v0>
 *
 * @extends {LitComponent}
 */
import { definition } from '@eui/component';
import { LitComponent, html } from '@eui/lit-component';
import { EDSPagination } from './EDSPagination';

import style from './pagination.css';

/**
 * @property {number} currentPage - position of the current Page
 * @property {number} numPages - number of pages
 * @property {number} numEntries - number of entries
 * @property {boolean} compOrientation - sets the orientation of component (horizontal / vertical)
 * @property {boolean} compDirection - sets the mode of component (reverse / normal)
 */
@definition(`eui-pagination-v${__VERSION__}`, {
  style,
  props: {
    currentPage: { attribute: true, type: 'number', default: 1 },
    numEntries: { attribute: true, type: 'number', default: 1 },
    numPages: { attribute: true, type: 'number', default: 1 },
    compOrientation: { attribute: true, type: 'string', default: 'horizontal' },
    compDirection: { attribute: true, type: 'string', default: 'normal' },
  },
})
export default class Pagination extends LitComponent {
  /**
   * Determine which arrow icons to use depending on orientation and direction.
   * @function _setArrows
   * @private
   */
  _setArrows = () => {
    const leftArrow = this.shadowRoot.querySelector('.pagination li.left-arrow');
    const rightArrow = this.shadowRoot.querySelector('.pagination li.right-arrow');

    if (this.compOrientation === 'vertical') {
      if (this.compDirection === 'normal') {
        // vertical + normal
        leftArrow.querySelector('eui-v0-icon').name = 'arrow-down';
        rightArrow.querySelector('eui-v0-icon').name = 'arrow-up';
      } else {
        // vertical + reverse
        leftArrow.querySelector('eui-v0-icon').name = 'arrow-up';
        rightArrow.querySelector('eui-v0-icon').name = 'arrow-down';
      }
    } else {
      // default: horiztonal + normal
      leftArrow.querySelector('eui-v0-icon').name = 'arrow-left';
      rightArrow.querySelector('eui-v0-icon').name = 'arrow-right';
    }
  }

  handleEvent(event) {
    if (event.type === 'pageIndexChange') {
      const evState = event.detail.state;
      this.currentPage = (evState.currentPage !== this.currentPage)
                          ? evState.currentPage : this.currentPage;
      this.numPages = (evState.numPages !== this.numPages)
                          ? evState.numPages : this.numPages;
      this.numEntries = (evState.numEntries !== this.numEntries)
                          ? evState.numEntries : this.numEntries;
    }
  }

  componentDidReceiveProps(prev) {
    if (this.edsPagination) {
      if (prev.currentPage === this.edsPagination.state.currentPage
        && prev.currentPage !== this.currentPage) {
          this.edsPagination.state.currentPage = this.currentPage;
          this.edsPagination.triggerChange();
        } else {
          this.currentPage = this.edsPagination.state.currentPage;
        }

        if ((this.numPages !== this.edsPagination.state.numPages)
        || (this.numEntries !== this.edsPagination.state.numEntries)) {
          this.edsPagination.state.numPages = this.numPages;
          this.edsPagination.state.numEntries = this.numEntries;
          this.edsPagination.injectPaginationControls('update');
          this.edsPagination.triggerChange();
        }
      }
      super.componentDidReceiveProps(prev);
    }

    componentDidRender(prev) {
    if (!this.edsPagination) {
      const paginationGroupElement = this.shadowRoot.querySelector('.pagination-group');
      this.edsPagination = new EDSPagination(paginationGroupElement);
      this.edsPagination.state.currentPage = this.currentPage;
      this.edsPagination.state.numEntries = this.numEntries;
      this.edsPagination.init(this.numPages);
    }

    if ((this.compOrientation !== prev.compOrientation)
    || (this.compDirection !== prev.compDirection)) {
      this._setArrows();
    }

    super.componentDidRender(prev);
  }

  /**
   * Render the Pagination component. This function is called each time a
   * prop changes.
   */
  render() {
    return html`
    <style>  
      :host {  
        display: inline-block;  
      }  
    </style>
    <div @pageIndexChange=${this} class="pagination-group ${this.compOrientation}">
      <ul class="pagination ${this.compDirection}"></ul>
    </div>
    `;
  }
}

Pagination.register();
