import '@eui/base';
import { decoratorMixin } from '../utilities/decoratorMixin';

export const resizableHeader = decoratorMixin({
  /**
   * handles initialization for resizable table header
   *
   * @function _initResizableTableHeader
   *
   */
  _initResizableTableHeader() {
    if (this.resizable) {
      this._colWidths = [];
      this.startX = undefined;
      this._dragging = false;
      this._pressed = false;
      this.mousemoveHandler = null;
      this.currAvailableTblCntWidth = 0;
      this._currentColWidths = [];
      this._mouseEndHandler = this._mouseEnd.bind(this);
      this._handleResizeHandler = this._handleResize.bind(this);
      this.startW = 0;
      document.addEventListener('mouseup', this._mouseEndHandler);
      document.addEventListener('mouseleave', this._mouseEndHandler);
      document.addEventListener('mousemove', this._preventDefault, false);
      window.addEventListener('resize', this._handleResizeHandler);
      } else {
      document.removeEventListener('mouseup', this._mouseEndHandler);
      document.removeEventListener('mouseleave', this._mouseEndHandler);
      document.removeEventListener('mousemove', this._preventDefault, false);
      window.removeEventListener('resize', this._handleResizeHandler);
    }
  },

  /**
   * set column widths in resizable header table
   *
   * @function _setColWidths
   *
   */
  _setColWidths() {
    this._colWidths = [];
    const tableContainer = this.shadowRoot.querySelector('.tableContainer');
    if (tableContainer) {
      // table original width should be same as table container
      this.currAvailableTblCntWidth = tableContainer.clientWidth;
    }
    if (this.columns && this.columns.length > 0) {
      const headerCells = this.shadowRoot.querySelectorAll('thead th');
      if (headerCells && headerCells.length >= this.columns.length) {
        for (let i = 0; i < headerCells.length - 1; i += 1) {
            // traversing -1 times to leave out dummycolumn
            this._colWidths[i] = headerCells[i].offsetWidth;
            headerCells[i].style.width = `${this._colWidths[i]}px`;
            // this._originalTotalWidth += this._colWidths[i];
        }
      }
      this._currentColWidths = [...this._colWidths];
    }
  },

  /**
   * handles mousedown event of column to be resized
   *
   * @function _mousedown
   *
   * @param {object} e - mouse event object
   */
  _mousedown(e) {
    e.preventDefault();
    e.stopPropagation();
    const isResizeAnchor = e.target.classList.contains('th__resizeAnchor');
    const isResizeLeftAnchor = e.target.classList.contains('th__resizeAnchor--left');
    if (isResizeAnchor) {
      this._resizeStart(e.srcElement.parentElement, (e.clientX || e.pageX));
    } else if (isResizeLeftAnchor) {
      const columnIndex = e.srcElement.parentElement.cellIndex - 1;
      const tableHeaders = this.shadowRoot.querySelectorAll('thead th');
      if (columnIndex >= 0) {
        this._resizeStart(tableHeaders[columnIndex], (e.clientX || e.pageX));
      }
    }
  },

  /**
   * handles initialization for column to be resized,
   * attaches mousemove handler
   *
   * @function _resizeStart
   *
   * @param {object} column - column to be resized
   * @param {number} startPosition - starting position of mouse before resizing
   */
  _resizeStart(column, startPosition) {
    this._pressed = true;
    this.startX = startPosition;
    this.startW = column.offsetWidth;
    this.mousemoveHandler = (event) => {
      this._mousemove(event, column);
    };
    this.addEventListener('mousemove', this.mousemoveHandler);
  },

  /**
   * triggers when resizing is done on mouseup mouseleave event,
   * clears variable and eventhandler set during mousemove
   *
   * @function _mouseEnd
   *
   */
  _mouseEnd() {
    if (this._pressed) {
      this.removeEventListener('mousemove', this.mousemoveHandler);
      this._dragging = false;
      this._pressed = false;
      this._colWidths = [];
    }
  },

  /**
   * handles mousemove event of column to resize
   *
   * @function _mousemove
   *
   * @param {object} e -mouse event object
   * @param {object} column - column to resize
   */
  _mousemove(e, column) {
    if (this._pressed) {
      this._dragging = true;
      const moveX = (e.clientX || e.pageX);
      const diffX = (this.startX - moveX);
      if (diffX === 0) {
        return;
      }
      const maxWidth = Math.max(this.startW - diffX, 60);
      this._currentColWidths[column.cellIndex] = maxWidth;
      this._updateTable(column);
    }
  },

  /**
   * update column/s width on resize
   *
   * @function _updateTable
   *
   * @param {object} column - column to resize
   */
  _updateTable(column) {
    const currTotal = this._currentColWidths.reduce((a, b) => a + b);
    const extraColumnCell = this.shadowRoot.querySelector('th.dummyColumn');
    const table = this.shadowRoot.querySelector('table');
    table.style.minWidth = `${currTotal}px`;

    if (currTotal >= this.currAvailableTblCntWidth) {
      column.style.width = `${this._currentColWidths[column.cellIndex]}px`;
    } else {
        extraColumnCell.style.width = `${(this.currAvailableTblCntWidth - currTotal)}px`;
        column.style.width = `${this._currentColWidths[column.cellIndex]}px`;
    }
  },

  _handleResize() {
    const tableContainer = this.shadowRoot.querySelector('.tableContainer');
    if (tableContainer) {
      this.currAvailableTblCntWidth = tableContainer.clientWidth;
    }
    const currTotal = this._currentColWidths.reduce((a, b) => a + b);
    const extraColumnCell = this.shadowRoot.querySelector('th.dummyColumn');
    if (extraColumnCell) {
      if (this.currAvailableTblCntWidth >= currTotal) {
        extraColumnCell.style.width = `${(this.currAvailableTblCntWidth - currTotal)}px`;
      } else {
          extraColumnCell.style.width = 0;
      }
    }
  },
  /**
   * TODO: commenting out double click behaviour
   * to enable as part of future requirement
   * */
  // _onDblClick(event) {
  //   let currIndex = event.target.parentElement.cellIndex;
  //   if (event.srcElement.className === 'th__resizeAnchor--left') {
  //     currIndex -= 1;
  //   }
  //   const headerRow = this.shadowRoot.querySelectorAll('thead th');
  //   const rows = this.shadowRoot.querySelectorAll('tbody tr');
  //   let requiredWidth = 0;
  //   if (rows) {
  //     rows.forEach((row) => {
  //       const targetColumn = row.cells && row.cells[currIndex];
  //       const textspan = targetColumn.querySelector('span');
  //       if (textspan) {
  //         const width = textspan.offsetWidth;
  //         const computedStyle = window.getComputedStyle(targetColumn);
  //         const totalWidth = width + parseInt(computedStyle.paddingLeft, 10)
  //       + parseInt(computedStyle.paddingRight, 10) + parseInt(computedStyle.borderLeftWidth, 10)
  //         + parseInt(computedStyle.borderRightWidth, 10) + 2;
  //         // added 2px extra to compensate for resizable header border
  //         if (totalWidth > requiredWidth) {
  //           requiredWidth = totalWidth;
  //         }
  //       }
  //     });
  //   }
  //   if (requiredWidth && requiredWidth !== headerRow[currIndex].offsetWidth) {
  //     this._currentColWidths[currIndex] = requiredWidth;
  //     this._updateTable(headerRow[currIndex]);
  //   }
  // },
});
