/**
 * Component CustomTableOne is defined as
 * `<e-custom-table-one>`
 *
 * Imperatively create component
 * @example
 * let component = new CustomTableOne();
 *
 * Declaratively create component
 * @example
 * <e-custom-table-one></e-custom-table-one>
 *
 * @extends {Table}
 */
import { definition } from '@eui/component';
import { html } from '@eui/lit-component';
import '@eui/base';
import Table from '../table/Table';
import style from './customTableOne.css';

/**
 * @property {String} columnName - Name of the column to place the custom cell.
 */
@definition('e-custom-table-one', {
  style,
  props: {
    columnName: { attribute: true, type: String },
  },
})
export default class CustomTableOne extends Table {
  /**
   * Clear the value of the textfield on the row
   *
   * @function _clearCellAction
   * @param {String} cellId - id to identify the cell
   * @private
   */
  _clearCellAction(cellId) {
    const textfield = this.shadowRoot.querySelector(`div#${cellId} eui-base-v0-text-field`);
    textfield.value = '';
  }

  /**
   * Make the value in the text field uppercase
   *
   * @function _makeUppercaseAction
   * @param {String} cellId - id to identify the cell
   * @private
   */
  _makeUppercaseAction(cellId) {
    const textfield = this.shadowRoot.querySelector(`div#${cellId} eui-base-v0-text-field`);
    textfield.value = textfield.value.toUpperCase();
  }

  /**
   * Override cell from base class. Called each time a cell should be rendered.
   * This implementation uses the prop; columnName to allow the user/developer
   * to place the cell in a specific column.
   *
   * @function cell
   * @param {Object} row - Row data.
   * @param {Object} column - Column definition.
   * @param {Number} rowIndex - The row index of the cell.
   * @param {Object} colIndex - The column index of the cell.
   * @returns contents of cell.
   */
  cell(row, column, rowIndex, colIndex) {
    if (column.attribute === this.columnName) {
      const cellId = `cell-${rowIndex}-${colIndex}`;
      return html`
      <div id=${cellId} class="custom-cell">
        <eui-base-v0-text-field 
        class="custom-cell__textfield"
        placeholder="Insert cell informtion"
        value="${row[column.attribute]}"
        @input=${(event) => {
          row[column.attribute] = event.currentTarget.value;
        }}></eui-base-v0-text-field>
          <eui-base-v0-dropdown
            class="custom-cell__more"
            dataType="single"
            more
          >
          <div id="clear-field-action" menu-item @click=${() => this._clearCellAction(cellId)} tabindex="0">Clear field</div>
          <div id="make-uppercase-action" menu-item @click=${() => this._makeUppercaseAction(cellId)} tabindex="0">Make uppercase</div>
        </eui-base-v0-dropdown>
        </div>
      `;
    }
    return super.cell(row, column);
  }
}
CustomTableOne.register();
