/**
 * Component Table is defined as
 * `<eui-table-v0>`
 *
 * Imperatively create component
 * @example
 * let component = new Table();
 *
 * Declaratively create component
 * @example
 * <eui-table-v0></eui-table-v0>
 *
 * @extends {LitComponent}
 */
import { definition } from '@eui/component';
import { LitComponent, html, ifDefined } from '@eui/lit-component';
import { selectable } from '../extensions/selectable';
import { sortable } from '../extensions/sortable';
import { resizableHeader } from '../extensions/resizableHeader';
import style from './table.css';

/**
 * @property {Array} columns - column data.
 * @property {Boolean} compact - set the table to compact style.
 * @property {Array} data - table data.
 * @property {Boolean} dashed - set the table to dashed style.
 * @property {Boolean} multiSelect - enable multi-select on the table.
 * @property {Boolean} tiny - set the table to tiny style.
 * @property {Boolean} resizable - enables resizable headers on the table
 * @property {Boolean} singleSelect - enable single-select on the table.
 * @property {Boolean} sortable - enable sortable table.
 * @property {Boolean} striped - set the table to striped style.
 * @property {Boolean} fixed - set the table to have a fixed header
 * @property {Number} fixedHeight - optionally sets the height of the table body when fixed
 * @property {Boolean} noHeader - makes a table headerless
 */
@selectable
@sortable
@resizableHeader
@definition(`eui-table-v${__VERSION__}`, {
  style,
  props: {
    columns: { attribute: false, type: Array, default: [] },
    compact: { attribute: true, type: Boolean },
    data: { attribute: false, type: Array, default: [] },
    dashed: { attribute: true, type: Boolean },
    multiSelect: { attribute: true, type: Boolean },
    tiny: { attribute: true, type: Boolean },
    singleSelect: { attribute: true, type: Boolean },
    sortable: { attribute: true, type: Boolean },
    striped: { attribute: true, type: Boolean },
    fixed: { attribute: true, type: Boolean },
    fixedHeight: { attribute: true, type: String },
    noHeader: { attribute: true, type: Boolean, default: false },
    resizable: { attribute: true, type: Boolean, default: false },
  },
})
export default class Table extends LitComponent {
  constructor() {
    super();
    this._makeColumnHead = this._makeColumnHead.bind(this);
    this._makeTableRows = this._makeTableRows.bind(this);
    this._handleRowClick = this._handleRowClick.bind(this);
    this._handleRowKeyPress = this._handleRowKeyPress.bind(this);
    this._updateSelectionRange = this._updateSelectionRange.bind(this);
    this.prevRowIndex = undefined;
    this.cell = this.cell.bind(this);

    // The default fixed height applied to a fixed heading table
    this.defaultFixedHeight = 400;
  }

  didConnect() {
    if (this.multiSelect) {
      this._updateDataWithSelectedState();
    }
  }

  didChangeProps(changedProps) {
    if (changedProps.has('singleSelect') && !this.singleSelect) {
      const temp = this.data.filter(r => r.selected === true);
      temp.forEach((element) => {
          element.selected = false;
      });
      this.data = [...this.data];
    }

    if (changedProps.has('resizable')) {
      this._initResizableTableHeader();
    }
  }

  /**
   * lifecycle hook -triggers everytime changes flushed to DOM
   * rendering done
   *
   * @function didRender
   *
   */
  didRender(changedProps) {
      if (this.resizable) {
          this._setColWidths();
      }

    // If multiselect = true and the data changes
    // then check if the header checkbox needs to be updated.
    // if (this.multiSelect && prev.data && prev.data.length !== this.data.length) {
    //   this._isSelected();
    // }
    if (this.multiSelect && changedProps.has('data')) {
      this._isSelected();
    }

    // If multiSelect is set to false, mark all rows as unselected
    if (changedProps.has('multiSelect') && this.multiSelect === false) {
      this._updateDataWithSelectedState();
    }
  }

  /**
   * lifecycle hook
   * triggers just before component is about to be disconnected from DOM
   *
   * @function componentWillDisconnect
   *
   */
  componentWillDisconnect() {
    document.removeEventListener('mouseup', this._mouseEndHandler);
    document.removeEventListener('mouseleave', this._mouseEndHandler);
    document.removeEventListener('mousemove', this._preventDefault, false);
  }

  /**
   * prevents selected text from being dragged
   *
   * @function _preventDefault
   *
   */
  _preventDefault(e) {
    e.preventDefault();
    e.stopPropagation();
  }

  /**
   * Calculates the value for fixedHeight prop
   *
   * @function _calcFixedHeight
   * @private
   */
  _getFixedHeight() {
    if (this.fixedHeight > 0) {
      return `${this.fixedHeight}px`;
    }

    if (this.fixedHeight === 'auto') {
      return '100%';
    }

    return `${this.defaultFixedHeight}px`;
  }

   /**
   * Updates the data with a single selected row
   *
   * @function _updateSingleSelection
   * @param {Object} row - row of column data
   * @private
   */
  _updateSingleSelection(row) {
    const temp = this.data.filter(r => r.selected === true);
    temp.forEach((element) => {
      if (element !== row) {
        element.selected = false;
      }
    });
  }

  /**
   * Updates the data with a single selected row
   *
   * @function _updateSelectionRange
   * @param {Number} lowerIndex - lower index (inclusive) of the range of selected rows
   * @param {Number} upperIndex - upper index (inclusive) of the range of selected rows
   * @param {Boolean} concatRange - add this range to already selected rows or not?
   * @private
   */
  _updateSelectionRange(lowerIndex, upperIndex, concatRange) {
    this.data.forEach((row, index) => {
      if (index >= lowerIndex && index <= upperIndex) {
        row.selected = true;
      } else {
        row.selected = (!concatRange) ? false : row.selected;
      }
    });
  }

  /**
   * Selects checkbox and row on row click
   *
   * @function _handleRowClick
   * @param {Object} row - row of column data
   * @private
   */
  _handleRowClick(row, event, index = 0) {
    const { shiftKey } = event;
    const { metaKey } = event; // cmd key on Mac
    let ctrlKey = (!metaKey) ? event.ctrlKey : metaKey;

    // Treat checkbox click as CTRL+click
    if (event.target && event.target.nodeName.toLowerCase() === 'eui-base-v0-checkbox') {
      ctrlKey = true;
    }

    // Multi-select
    if (this.multiSelect) {
      if (shiftKey && this.prevRowIndex !== index) {
        // if shift click, highlight range of rows
        const indexLower = (this.prevRowIndex < index) ? this.prevRowIndex : index;
        const indexUpper = (this.prevRowIndex < index) ? index : this.prevRowIndex;
        this._updateSelectionRange(indexLower, indexUpper, ctrlKey);
      } else {
        // set the index (for shift click above)
        this.prevRowIndex = index;

        if (!ctrlKey) {
          if (!((this.data.filter(r => r.selected === true).length > 1) && row.selected)) {
            row.selected = row.selected === undefined ? true : !row.selected;
          }
          this._updateSingleSelection(row);
        } else if (
          (event.target
            && (event.target.nodeName.toLowerCase() !== 'eui-base-v0-checkbox'
              && event.currentTarget.nodeName.toLowerCase() === 'tr'))
            || (event.target === undefined)) {
            // if CTRL+click, and it wasn't a click on the checkbox...
            row.selected = row.selected === undefined ? true : !row.selected;
        }
      }
    } else if (this.singleSelect) {
      // Single-select
      row.selected = row.selected === undefined ? true : !row.selected;
      this._updateSingleSelection(row);
    }

    this.data = [...this.data];

    // Bubble 'row-selected' event only for multiSelect and singleSelect
    if (this.multiSelect || this.singleSelect) {
      setTimeout(() => {
        this.bubble('row-selected', this.selectedRows());
      }, 0);
    }

    // Bubble 'row-click' whenever a row is clicked
    setTimeout(() => {
      this.bubble('row-click', row);
    }, 0);
  }

  /**
   * selects checkbox and row on spacebar press
   *
   * @function __handleRowKeyPress
   * @param {object} row -row of column date
   * @param {object} event - bubbled event from keypress on row
   * @private
   */
  _handleRowKeyPress(row, event, index) {
    if (event.keyCode === 32) {
      if (event.preventDefault) {
        event.preventDefault();
      }
      this._handleRowClick(row, event, index);
    }
  }

  /**
   * Create the table heading.
   *
   * @function _makeColumnHead
   * @private
   */
  _makeColumnHead() {
    return this.columns.map((th, index) => this._columnHeading(th, index));
  }

  /**
   * handles click on table header.
   *
   * @function _handleColumnClick
   * @private
   */
  _handleColumnClick(index, isColumnSortable) {
    if (this.sortable && isColumnSortable) {
      this._sortColumn(index);
    }
  }

  /**
   * Create a column heading
   *
   * @param {Object} column - Definition of a column
   * @private
   */
  _columnHeading(column, index) {
    const width = column.width ? `width:${column.width}` : '';
    const thClassList = column.sortable ? 'eui__table__th eui__table__th--sortable' : 'eui__table__th';

    const sortableIcon = this.sortable && column.sortable
      ? html`<eui-icon id="icon-${column.attribute}" name="${this._iconState(index)}" size="14px"></eui-icon>` : '';

    let resizableDivElemLeft = this.resizable && column.resizable ? html`<div class="th__resizeAnchor--left"
        @mousedown=${this._mousedown.bind(this)}
        @click=${e => e.stopPropagation()}></div>` : '';

    const resizableDivElem = this.resizable && column.resizable ? html`<div class="th__resizeAnchor"
        @mousedown=${this._mousedown.bind(this)}
        @click=${e => e.stopPropagation()}></div>` : '';

        if (this.multiSelect && index === 0) {
          resizableDivElemLeft = '';
        }

    return html`<th class="${thClassList}"
                  id=${column.attribute}
                  @click=${() => this._handleColumnClick(index, column.sortable)}
                  ?resizable=${column.resizable}
                  style="${width}">
                    ${resizableDivElem}   
                    <div class="header">
                      <span class="header__column__title" title="${column.title}">
                        ${column.title}
                      </span>
                      ${sortableIcon}
                    </div>
                    ${resizableDivElemLeft}
                </th>`;
  }

  /**
   * Override this function when you require custom cell handling.
   * This function takes the value from the row with the corresponding cell attribute.
   * @function cell
   * @param {Object} row - row data.
   * @param {Object} column - column definition.
   */
  cell(row, column) {
    return html`<span>${row[column.attribute]}</span>`;
  }

  /**
   * Create the table rows.
   *
   * @function _makeTableRows
   * @private
   */
  _makeTableRows() {
    return this.data.map((row, rowIndex) => html`
      <tr 
        @click=${event => this._handleRowClick(row, event, rowIndex)}
        @keyup=${event => this._handleRowKeyPress(row, event, rowIndex)}
        tabindex="0"
        ?selected=${row.selected}
      >
      ${this.multiSelect ? this._addRowCheckbox(row) : ''}
      ${this.columns.map((column, colIndex) => html`<td>${this.cell(row, column, rowIndex, colIndex)}</td>`)}
        ${ifDefined(this.resizable ? html`<td class="dummyColumn"></td>` : undefined)}
      </tr>`);
  }

  /**
   * Render the <eui-table-v0> component. This function is called each time a
   * prop changes.
   */
  render() {
    if (this.columns && this.columns.length <= 0) {
      return false;
    }

    this.style.height = this.fixed && this.fixedHeight === 'auto' ? '100%' : '';

    const tableHead = this.noHeader ? undefined : html`<thead>
        <tr>
        ${this.multiSelect ? this._addHeaderCheckbox() : ''} ${this._makeColumnHead()}
        ${ifDefined(this.resizable ? html`<th class="dummyColumn"></th>` : undefined)}
        </tr>
      </thead>`;

    const tableBody = html`<tbody>
                            ${this._makeTableRows()}
                           </tbody>`;

    return html`<div class="tableContainer"
                    style="${ifDefined(this.fixed ? `max-height:${this._getFixedHeight()}` : undefined)}">
                    <table>
                      ${ifDefined(tableHead)}
                      ${tableBody}
                    </table>
                  </div>`;
  }
}

Table.register();
